
import  util  from '../util/util'
import { recordBook } from "@/api/index";
import dateFormat from 'vux/src/tools/date/format.js'

export default {
data () {
return {
username: '',
mobile: '',
company: '',
post: '',
tiems: '',
count: '',
begin: '',
end: '',
number: '',
alls: '',
values: '',
first: [{title:'京瓷哲学'}],
pass: true,
pickerValue: ''
}
},
methods: {
addbook () {
this.first.push({})
},
delbook (i) {
this.first.splice(i,1)
},
getTime () {
this.tiems = dateFormat(new Date(), 'YYYY-MM-DD')
this.begin = dateFormat(new Date(), 'YYYY-MM-DD')
},
func (index) {
if (index == 0) {
return false
} else {
return true
}
},
record () {
this.pass = true
if(!this.username) {
return this.$vux.alert.show({
title: '请输入必填选项',
content: '请输入用户名',
})
} else if(!this.mobile) {
return this.$vux.alert.show({
title: '请输入必填选项',
content: '请输入手机号',
})

} else if(!this.tiems) {
return this.$vux.alert.show({
title: '请输入必填选项',
content: '请输入打卡始于时间',
})
} else if(!this.count) {
return this.$vux.alert.show({
title: '请输入必填选项',
content: '请输入打卡次数',
})
} else {
this.first.forEach( el => {
var arr = Object.keys(el)
if( el == {} || arr.length !== 3 ) {
this.pass = false;
return  this.$vux.alert.show({
title: '请输入必填选项',
content: '请填写或者删除多余空白书本',
})
}
})
}

if(this.pass  )  {
recordBook({name: this.username, phone: this.mobile, company: this.company, post: this.post, pushStart: this.tiems,total: this.count, bonaStart: this.begin, bonaEnd: this.end, bonaCount: this.number, bonaTotal: this.alls, volunteer: this.values, books: this.first}).then( res => {
alert(res.code)
console.log(res,131)
})
}

},
openPicker() {
this.$refs.picker.open();
},
},
watch:{
pickerValue(val) {
this.tiems = util.itcTiem(this.pickerValue)
}
},
created () {
this.getTime()
},
components: {
}
}



